import { Component, createRef } from 'react';
import { Redirect } from 'react-router-dom';
import Logger from './../Logger';
import { Input, Button, SideBar, ChatItem } from 'react-chat-elements';
import 'react-chat-elements/dist/main.css';
import profileImage from './profile.png';

import io from 'socket.io-client';
import generate from 'canihazusername';

export default class Home extends Component {
  state = {
    socket: null,
    messages: [],
    inputRef: createRef(),
    typingRef: createRef(),
    invalidUsername: false,
    isTyping: false,
    members: [],
    roomName: "unknown",
    admin: ""
  }

  componentDidMount() {
    Logger.info('Creating socket...');
    const socket = io("ws://141.94.71.203:8080", {
      transports: ["websocket"],
      autoConnect: false
    }).on('connect', () => {
      Logger.info('Connected to websocket');
      socket.emit('login', this.props.username);
    });

    Logger.info('Openning socket...');
    this.setState({ socket });
    socket.open();

    Logger.info('Socket openned');
    socket.on('message', (message, author) => {
      const messages = this.state.messages.slice();
      messages.push({
        author,
        message,
        date: new Date()
      });
      this.setState({ messages });
      const elem = document.querySelector('.content');
      elem.scrollTop = elem.scrollHeight;
    });

    socket.on('user join', (username) => {
      const messages = this.state.messages.slice();
      messages.push({
        author: "SkyChat",
        message: `${username} joined the room`,
        date: new Date()
      });
      const members = this.state.members.slice();
      members.push(username);
      this.setState({ messages, members });
    });

    socket.on('user leave', (username) => {
      const messages = this.state.messages.slice();
      messages.push({
        author: "SkyChat",
        message: `${username} left the room`,
        date: new Date()
      });
      const members = this.state.members.slice();
      members.splice(members.indexOf(username), 1);
      this.setState({ messages, members });
    });

    socket.on('typing', (username, isTyping) => {
      if (isTyping) {
        this.setState({ isTyping: true });
        // eslint-disable-next-line
        this.state.typingRef.current ? this.state.typingRef.current.style.display = 'block' : this.doNothing();
        // eslint-disable-next-line
        this.state.typingRef.current ? this.state.typingRef.current.innerHTML = `${username} is typing...` : this.doNothing();
      } else {
        this.setState({ isTyping: false });
        // eslint-disable-next-line
        this.state.typingRef.current ? this.state.typingRef.current.style.display = 'none' : this.doNothing();
      }
    });

    socket.on('room info', (roomName, admin, members) => {
      this.setState({ roomName, admin, members });
    });

    socket.on('room reset', () => {
      this.setState({ messages: [], roomName: 'unknown', admin: '', members: [] });
    });

    socket.on('invalid username', () => {
      this.setState({ invalidUsername: true });
      alert('Invalid username (already taken)');
    });
  }

  doNothing = () => {}

  componentWillUnmount() {
    if (this.state.socket) {
      this.state.socket.close();
    }
    this.setState({
      messages: [],
      socket: null,
      inputRef: null
    });
  }

  sendMessage = (e) => {
    e.preventDefault();
    this.state.socket.emit('message', this.state.inputRef.current.input.value);
    // eslint-disable-next-line
    this.state.inputRef.current.input.value = '';
  }

  sendTyping = () => {
    this.state.socket.emit('typing', true);
    this.setState({ isTyping: true });
  }

  render() {
    if (!this.props.loggedIn || !this.props.username) {
      return (
        <Redirect to="/" />
      );
    }

    if (this.state.invalidUsername) {
      return (
        <Redirect to="/" />
      );
    }

    return (
      <div className="chats-page">
        <div className="nav-bar">
          <div className="logo-tab">SkyChat</div>
          <div onClick={this.props.logOut} className="logout-tab">
            Logout
          </div>
        </div>
        {/* messages */}
        <div style={{ width: '20%', float: 'left' }}>
          <SideBar
            center={(
              <div>
                <span style={{ color: 'white', marginBottom: '1rem' }}>
                  Current Room: {this.state.roomName}
                </span>
                <Button text="Create a new room" onClick={() => this.state.socket.emit('create room', generate('{shape} {city_states}'))} />
              </div>
            )}
            bottom={(
              <div style={{ display: 'flex', flexDirection: 'row' }}>
                <input type="text" id="join-room" />
                <button className="join-room" onClick={() => this.state.socket.emit('join room', document.querySelector('#join-room').value)}>Join Room</button>
              </div>
            )}
            type='light' />
            <h3 style={{ marginLeft: '1rem' }}>Online users ({this.state.members.length}):</h3>
            <ul style={{ marginLeft: '1rem' }}>
              {this.state.members.map(member => {
                return (
                  <li key={generate()}>
                    {member}{member === this.state.admin ? ' - Admin' : ''}
                  </li>
                );
              })}
            </ul>
        </div>
        <div className="container">
          <div className="content">
            {this.state.messages.map(message => (
                <ChatItem
                  avatar={profileImage}
                  alt={message.author}
                  title={message.author}
                  subtitle={message.message}
                  date={message.date}
                  unread={0}
                  key={generate()} />
              ))}
          </div>
        </div>
        <div className="form-input-1">
          <span className="typing" ref={this.state.typingRef}>SkyDonald is typing...</span>
          <form>
            <Input
              placeholder="Type your message here..."
              multiline={false}
              inputStyle={{ backgroundColor: '#282C34', marginTop: this.state.isTyping ? 0 : 27 }}
              ref={this.state.inputRef}
              onChange={this.sendTyping}
              rightButtons={
                  <Button
                      color='white'
                      backgroundColor='#1D53AA'
                      text='Send'
                      onClick={this.sendMessage} />
              } />
          </form>
        </div>
      </div>
    );
  }
}
